



import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

let api_url = 'https://fakestoreapi.com/products'

export let fetchView = createAsyncThunk('view/fetchView',async()=>{
    let res = await axios.get(api_url)
    // console.log('axios response for post api is:',res);
    return res?.data;
})

export const fetchSub = createAsyncThunk('view/fetchSub',async(id)=>{
    let res = await axios.get(`${api_url}/${id}`);
    // console.log('axios response for single',res);
    return res?.data;
})

const initial_Value = {
    product_data:[],
    isLoading:false,
    err:null
}

export const viewSlice = createSlice({
    name:'view',
    initialState:initial_Value,

    extraReducers:(builder)=>{

        //view product data

        builder.addCase(fetchView.pending, (state,action)=>{
            state.isLoading = true;
        })

        builder.addCase(fetchView.fulfilled, (state,action)=>{
            state.isLoading = false;
            state.product_data = action.payload;
            state.err = null
        })

        builder.addCase(fetchView.rejected, (state,action)=>{
            state.isLoading = false;
            state.product_data = [];
            state.err = action.error.message
        })

    }
})

export default viewSlice.reducer;